#showcase {
    font-family: "Segoe UI";
    background: rgb(78, 24, 179) url(../../assets/contracosta.png) no-repeat center center/cover;
    height: 100vh;
    color: #fff;
}

#showcase .showcaseGradient {
    height: 100%;
    padding: 0, 0rem;
    /* overlay effect */
    background: linear-gradient( to bottom, rgba(0, 122, 179, 0) 60%, rgba(0, 122, 179, 1));
    top: 80px;
    left: 0px;
}

#showcase .showcaseContent {
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}

.wrap {
    display: flex;
    width: 50%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.wrap1 {
    display: flex;
}

.wrap1 div {
    margin: 0.5vw;
}

.wrap1 div a {
    display: flex;
}

.wrap h1 {
    font-size: 4.5vw;
    margin-bottom: 1vw;
    line-height: 1.1;
}

.wrap p {
    font-size: 1.5vw;
    margin-bottom: 2vw;
}

.btn1 {
    padding: 1vw;
    align-items: center;
    color: #fff;
    background: #007ab3;
    border: #007ab3;
    border-radius: 1px;
    border-style: solid;
    border-width: 0.3rem;
    font-size: 1vw;
    text-decoration: none;
    transition: all 0.4s ease-in-out;
    height: 5vw;
}

.btn2 {
    padding: 1vw;
    align-items: center;
    display: inline-block;
    color: #fff;
    background: #f5a30c;
    border: #f5a30c;
    border-radius: 1px;
    border-style: solid;
    border-width: 0.3rem;
    font-size: 1vw;
    text-decoration: none;
    height: 5vw;
    transition: all 0.4s ease-in-out;
}

.btn3 {
    padding: 1vw;
    align-items: center;
    display: inline-block;
    color: #fff;
    background: #910f25;
    border: #910f25;
    border-radius: 1px;
    border-style: solid;
    border-width: 0.3rem;
    font-size: 1vw;
    text-decoration: none;
    height: 5vw;
    transition: all 0.4s ease-in-out;
}

.btn1:hover {
    background: rgba(33, 36, 179, 0);
    border: #007ab3;
    border-style: solid;
    border-width: 0.3rem;
}

.btn2:hover {
    background: rgba(33, 36, 179, 0);
    border: #007ab3;
    border-style: solid;
    border-width: 0.3rem;
}

.btn3:hover {
    background: rgba(33, 36, 179, 0);
    border: #007ab3;
    border-style: solid;
    border-width: 0.3rem;
}

.fadeUp {
    opacity: 0;
    animation: fadeAnimation 0.4s forwards;
}

@keyframes fadeAnimation {
    0% {
        opacity: 0;
        transform: translate(10px, 10px);
    }
    75% {
        opacity: 0.2;
    }
    100% {
        opacity: 1;
        transform: translate(0px, 0px);
    }
}

.wrap h1 {
    animation-delay: 0.1s;
}

.wrap p {
    animation-delay: 0.2s;
}

.wrap div {
    animation-delay: 0.3s;
}

@media (max-width: 600px) {
    #showcase {
        font-family: "Segoe UI";
        background: rgb(78, 24, 179) url(../../assets/contracosta_small.png) no-repeat center center/cover;
        height: 50vh;
        color: #fff;
    }
    .wrap {
        width: 100vh;
    }
    .wrap1 {
        justify-content: center;
    }
    .wrap1 div {
        margin: 4vw;
    }
    .wrap h1 {
        font-size: 6vw;
        margin-bottom: 3vw;
        line-height: 1.1;
    }
    .wrap p {
        font-size: 3vw;
        margin-bottom: 1vw;
    }
    .btn1 {
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all 0.4s ease-in-out;
        width: 25vw;
        height: 25vw;
        padding: 0;
        border-radius: 50%;
        font-size: 2vw;
    }
    .btn2 {
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all 0.4s ease-in-out;
        width: 25vw;
        height: 25vw;
        padding: 0;
        border-radius: 50%;
        font-size: 2vw;
    }
    .btn3 {
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all 0.4s ease-in-out;
        width: 25vw;
        height: 25vw;
        padding: 0;
        border-radius: 50%;
        font-size: 2vw;
    }
}