.topToBtm {
    position: relative;
}

.iconPosition {
    position: fixed;
    bottom: 40px;
    right: 25px;
    z-index: 20;
}

.iconStyle {
    background-color: #053e79;
    border: 2px solid #fff;
    border-radius: 50%;
    height: 50px;
    width: 50px;
    color: #fff;
    cursor: pointer;
    animation: movebtn 1s ease-in-out infinite;
    transition: all 0.5s ease-in-out;
}

.iconStyle:hover {
    animation: none;
    background: #fff;
    color: #053e79;
    border: 2px solid #053e79;
}

@keyframes movebtn {
    0% {
        transform: translateY(0px);
    }
    25% {
        transform: translateY(10px);
    }
    50% {
        transform: translateY(0px);
    }
    75% {
        transform: translateY(-10px);
    }
    100% {
        transform: translateY(0px);
    }
}